import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StoresProvider } from '../../providers/stores/stores';

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage {
  myInput: string = null;
  storeslist = [];
  newStoreList = [];


  constructor(public navCtrl: NavController, public navParams: NavParams, public storeProvider:StoresProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchPage');
  }
  ionViewWillEnter() {
    this.getStoresList();

  }
    onInput(ev) {
      if (this.storeslist.length > 0) {
        let val = ev.target.value;
        if (val && val.trim()) {
          this.newStoreList = this.storeslist.filter((item) => {
            return (item.storename.toLowerCase().indexOf(val.toLowerCase()) > -1);
          })
        } else {
          this.newStoreList = [];
        } 
      }
    }
  getStoresList(){
    this.storeProvider.getAllStores().subscribe(res => {
      this.storeslist = res;
    })
  }

  selectStore(store) {
    this.myInput = store.storename;
    this.newStoreList = [];
  }

}



